import ListSession, {getFormattedTime} from "./ListSession";
import Modal from "../components/Modal";
import {def} from "./definitions";
import {useState} from "react";

export default function List(props) {
    let data = ""
    try {
        data = require(`./trainingplan_json/${props.file}.json`)
    } catch (error) {

    }
    const [selectedSessionId, setSelectedSessionId] = useState(0)
    const [detailsVisible, setDetailsVisibility] = useState(false)

    let selectedSession = data.sessions[selectedSessionId]

    const showDetails = (event, id) => {
        setSelectedSessionId(id)
        setDetailsVisibility(true)
    }
    const hideDetails = (event) => {
        setDetailsVisibility(false)
    }

    function addDays(count_days = 0) {
        const firstDay = props.startdate
        return new Date(new Date(firstDay).setDate(firstDay.getDate() + count_days));
    }

    return (
        data ?
            <div className='training-list'>
                {data.sessions.map((s, i) => (
                    s.duration > 0 && <ListSession
                        id={s.id}
                        key={i}
                        date={addDays(i)}
                        name={s.name}
                        method={s.method}
                        duration={s.duration}
                        ranges={[s.rangeKB, s.rangeGA, s.rangeEB, s.rangeSB, s.rangeK123, s.rangeK45]}
                        onClick={showDetails}
                    />
                ))}
                {selectedSession &&
                    <Modal
                        title={`${selectedSession.name} am ${addDays(selectedSessionId).toLocaleDateString()}`}
                        show={detailsVisible}
                        handleClose={hideDetails}
                    >
                        <p>{getFormattedTime(selectedSession.duration)}</p>

                        <h3>{selectedSession.method}</h3>
                        <p>{def[selectedSession.method]}</p>

                        <h3>Leistungsbereiche</h3>
                        <p>Kompensationsbereich: {selectedSession.rangeKB} min</p>
                        <p>Grundlagenausdauer: {selectedSession.rangeGA} min</p>
                        <p>Entwicklungsbereich: {selectedSession.rangeEB} min</p>
                        <p>Spitzenbereich: {selectedSession.rangeSB}min</p>
                        <p>Maximal- und Schnellkraftsbereich: {selectedSession.rangeK123} min</p>
                        <p>Kraftausdauer: {selectedSession.rangeK45} min</p>
                    </Modal>}
            </div>
            :
            <>
                <h2>Für diese Daten ist kein Trainingsplan verfügbar.</h2>
                <p>Bitte versuche eine andere Konstellation.</p>
            </>
    )
}
