import {BrowserRouter, Route, Switch} from "react-router-dom";
import Home from "./Home";
import Gallery from "./gallery/Gallery";
import Trainingplan from "./trainingplan/Trainingplan";
import WordQuiz from "./wordquiz/Wordquiz";
import Imprint from "./imprint/Imprint";
import Header from "./components/Header";
import {useState, useLayoutEffect} from 'react';
import Error from "./Error";
import Jokes from "./Jokes";
import TikTak from "./tiktak/TikTak";

export default function App() {

    function getWindowDimensions() {
        const {innerWidth: width, innerHeight: height} = window;
        return {
            width,
            height
        };
    }

    function useWindowDimensions() {
        const [windowDimensions, setWindowDimensions] = useState(getWindowDimensions());

        useLayoutEffect(() => {
            function handleResize() {
                setWindowDimensions(getWindowDimensions());
            }

            window.addEventListener('resize', handleResize);
            return () => window.removeEventListener('resize', handleResize);
        }, []);

        return windowDimensions;
    }
    const { height, width } = useWindowDimensions();
    return (
        <div className='app' style={{width: width, height: height}}>
            <BrowserRouter>
                <Header/>
                <Switch>
                    <Route path="/tiktak" exact><TikTak/></Route>
                    <Route path="/wordquiz" exact><WordQuiz/></Route>
                    <Route path="/gallery" exact><Gallery/></Route>
                    <Route path="/trainingplan" exact><Trainingplan/></Route>
                    <Route path="/imprint" exact><Imprint/></Route>
                    <Route path='/jokes' exact><Jokes/></Route>
                    <Route path='/error' exact><Error/></Route>
                    <Route path="/" exact><Home/></Route>
                    <Route path='/*'><Error/></Route>
                </Switch>
            </BrowserRouter>
        </div>
    );
}
