import {useState} from "react";
import "../styles/trainingplan.sass"
import Modal from "../components/Modal";
import List from "./List";

export default function Trainingplan() {
    const [guideVisible, setGuideVisiblity] = useState(false);
    const [showList, setShowList] = useState(false);
    const [trainingForm, setTrainingForm] = useState(
        {
            discipline: "Singleday",
            duration: "3",
            traininghours: "4",
            trainingdays: "2",
            competitionDate: new Date(Date.now()).toISOString().substring(0, 10)
        }
    )

    const handleFormChange = (event) => {
        const {name, value} = event.target
        setTrainingForm(prevFormData => {
            return {
                ...prevFormData,
                [name]: value
            }
        })
    };


    function inputValid() {
        if (trainingForm.discipline === "") return false
        if (trainingForm.competitionDate === "") return false
        if (trainingForm.duration === "") return false
        if (trainingForm.trainingdays === "") return false
        if (trainingForm.traininghours === "") return false
        return true
    }

    const handleGuide = () => {
        setGuideVisiblity(!guideVisible)
    }

    const handleBack = () => {
        setShowList(false)
    }
    const handleCalculation = () => {
        if (inputValid()) {
            setShowList(true)
        }
    }

    const getStartDate = (daysBefore, date = new Date()) => new Date(date - (1000 * 60 * 60 * 24 * daysBefore));


    return (
        <>
            <main className="center">
                {!showList ?
                    <form>
                        <label>Wettkampfdatum</label>
                        <input
                            value={trainingForm.competitionDate}
                            onChange={handleFormChange}
                            type="date"
                            name="competitionDate"
                            required
                        />
                        <label>Dauer</label>
                        <select
                            id="select-duration"
                            value={trainingForm.duration}
                            onChange={handleFormChange}
                            name="duration"
                        >
                            <option value={3}>3 Monate</option>
                            <option value={4}>4 Monate</option>
                            <option value={5}>5 Monate</option>
                        </select>
                        <label>Disziplin</label>
                        <select
                            id="select-discipline"
                            value={trainingForm.discipline}
                            onChange={handleFormChange}
                            name="discipline"
                        >
                            <option value="Singleday">Straßenrennen</option>
                            <option value="Timetrial">Rundstrecke</option>
                            <option value="Mountain">Berg</option>
                        </select>
                        <label>Trainingsstunden</label>
                        <select
                            id="select-traininghours"
                            value={trainingForm.traininghours}
                            onChange={handleFormChange}
                            name="traininghours"
                        >
                            <option value={4}>4 Stunden</option>
                            <option value={5}>5 Stunden</option>
                            <option value={6}>6 Stunden</option>
                            <option value={7}>7 Stunden</option>
                            <option value={8}>8 Stunden</option>
                            <option value={9}>9 Stunden</option>
                            <option value={10}>10 Stunden</option>
                            <option value={11}>11 Stunden</option>
                            <option value={12}>12 Stunden</option>
                        </select>
                        <label>Trainingstage</label>
                        <select
                            id="select-trainingdays"
                            value={trainingForm.trainingdays}
                            onChange={handleFormChange}
                            name="trainingdays"
                        >
                            <option value={2}>2 Tage</option>
                            <option value={3}>3 Tage</option>
                            <option value={4}>4 Tage</option>
                            <option value={5}>5 Tage</option>
                            <option value={6}>6 Tage</option>
                        </select>
                    </form> :
                    <List
                        startdate={getStartDate(trainingForm.duration * 28, new Date(trainingForm.competitionDate))}
                        file={`${trainingForm.discipline}Competition_${trainingForm.duration}m_${trainingForm.trainingdays}d_${trainingForm.traininghours}h`}
                    />
                }
            </main>

            {!showList ?
                <footer>
                    <button onClick={handleGuide}>Trainingspläne</button>
                    <button onClick={handleCalculation}>Los!</button>
                </footer> :
                <footer>
                    <button onClick={handleBack}>Einstellungen</button>
                </footer>
            }
            <Modal
                show={guideVisible}
                handleClose={handleGuide}
                title='Optimierte Trainingspläne für den Radsport'
            >
                <p>
                    Besonders im Amateurbereich des Radsports spielt die Vorbereitung zu den Wettkämpfen eine
                    wichtige
                    Rolle.
                    Die maximale Leistung auf dem Rad wird vor allem von der physischen Leistungsfähigkeit
                    beeinflusst.
                    Unterschiedliche Trainingsziele nehmen Einfluss auf die Trainingsinhalte der individuellen
                    Sportler:innen.
                    Mit der Modellierung personalisierter Pläne soll ein optimaler Trainingsverlauf für eine breite
                    Personengruppe ermöglicht werden. Um die Leistung zu steigern, wird diese Modellierung
                    nach Erkenntnissen der allgemeinen und radsportspezifischen Trainingswissenschaft die Einheiten
                    aufeinander abstimmen und so strukturieren,
                    dass alle Belastungsbereiche wettkampforientiert gewichtet werden. <br/>
                    Das Backend der Arbeit ist im Rahmen der Bachelorarbeit entstanden.
                </p>
            </Modal>
        </>
    );
}
