import Letter from "./Letter";
import {WORD_LENGTH} from "./constants";
import React from "react";

export function EmptyWord() {
    return (<div className={"word empty"}>
            {[...Array(WORD_LENGTH)].map((e, i) => <Letter key={i}>-</Letter>)}
        </div>)
}


export function InputWord({word, cName}) {
    let paddedWord = word.padEnd(WORD_LENGTH)
    return (<div className={"word input " + cName}>
        {[...paddedWord].map((character, i) => {
            return <Letter key={i} data={character}/>
        })}
    </div>)
}


export function GuessWord({word, solution}) {

    function getColoring() {
        let classes = Array(WORD_LENGTH).fill("unknown");
        let tempSolution = [...solution];
        [...word].forEach((character, i) => {
            if (solution[i] === character) {
                classes[i] = "correct"
                tempSolution[i] = " "
            } else if (!solution.includes(character)) {
                classes[i] = "wrong"
            }
        })
        for (let i = 0; i < word.length; i++) {
            if (classes[i] === "unknown") {
                if (tempSolution.includes(word[i])) {
                    classes[i] = "position"
                    tempSolution[tempSolution.indexOf(word[i])] = " "
                } else {
                    classes[i] = "wrong"
                }
            }
        }
        return classes
    }

    const classes = getColoring();

    return (<div className={"word"}>
            {[...word].map((char, i) => {
                return <Letter key={i} data={char} active={classes[i]}/>
            })}
        </div>);
}
