import Modal from "../components/Modal";
import {useState} from "react";
import '../styles/gallery.sass'
import {images} from "./gallery_images.json"

export default function Gallery() {
    const [imageIndex, setImageIndex] = useState(0);
    const [descriptionVisible, setDescriptionVisibility] = useState(false);
    const handleShow = () => setDescriptionVisibility(true)
    const handleClose = () => setDescriptionVisibility(false)

    const nextImage = () => {
        setImageIndex(n => (n + 1) % images.length)
    };

    return (<>
    <main className='center'>
        <img className='gallery-image' src={process.env.PUBLIC_URL + images[imageIndex].href}
             alt={images[imageIndex].title}/>
    </main>
    <footer>
        <button onClick={handleShow}>{`${images[imageIndex].title}, ${images[imageIndex].year}`}</button>
        <button onClick={nextImage}>Mehr</button>
    </footer>
    <Modal
        show={descriptionVisible}
        handleClose={handleClose}
        title={`${images[imageIndex].title}, ${images[imageIndex].year}`}
        >
    <p>{images[imageIndex].description}</p>
    </Modal>
</>
)
    ;
}
