import '../styles/tiktak.sass'
export default function Clock({mDeg, hDeg}) {
    return (
        <div className='clock'>
            <div className='middle'></div>
            <div className='hour' style={{transform : `rotate(${hDeg}deg)`}}></div>
            <div className='minute' style={{transform : `rotate(${mDeg}deg)`}}></div>
        </div>
    );
}
