import axios from 'axios';
import {useEffect, useState} from "react";

export default function Jokes() {
    const [joke, setJoke] = useState("")

    useEffect(() => {
        axios.get(`https://official-joke-api.appspot.com/random_joke`)
            .then(res => {
                setJoke(res.data);
            })
    }, [])

    return (
        <>
            <h2>{joke.setup}</h2>
            <p>{joke.punchline}</p>
        </>
    )
}
