import React, {useEffect, useState} from 'react';
import Modal from '../components/Modal';
import {EmptyWord, GuessWord, InputWord} from './Word';
import wordlist from './words.json';
import '../styles/wordquiz.sass';
import {MAX_GUESSES, WORD_LENGTH} from './constants';
import Switch from './Switch';
import Alert from "../components/Alert";

export default function Wordquiz() {
    const [solution, setSolution] = useState(JSON.parse(localStorage.getItem("wordquiz-solution")) || 'butze');
    const [guessedWords, setGuessedWords] = useState(JSON.parse(localStorage.getItem("wordquiz-words")) || []);
    const [hardMode, setHardMode] = useState(JSON.parse(localStorage.getItem("wordquiz-hardmode")) || false);
    const [stats, setStats] = useState(JSON.parse(localStorage.getItem("wordquiz-stats")) || [])
    const [inputWord, setInputWord] = useState('');
    const [inputClass, setInputClass] = useState('active');
    const [guideVisible, setGuideVisiblity] = useState(false);
    const [statsVisible, setStatsVisiblity] = useState(false);
    const [gameWon, setGameWon] = useState(guessedWords.includes(solution));
    const [alertMessage, setAlertMessage] = useState("")
    const [gameRunning, setGameRunning] = useState(guessedWords.length < MAX_GUESSES && !gameWon);

    let guessesLeft = MAX_GUESSES - guessedWords.length
    let emptyRows = guessesLeft > 1 && [...Array(guessesLeft - 1)].map((e, i) => <EmptyWord key={i}/>)
    let guessRows = guessedWords.map((word, i) => <GuessWord key={i} word={word} solution={solution}
                                                             guessedWords={guessedWords}/>)

    const handleGuide = () => setGuideVisiblity(!guideVisible)
    const handleStats = () => setStatsVisiblity(!statsVisible)
    const handleHardMode = () => setHardMode(!hardMode)

    let inputField = React.createRef();

    function getCorrectLetters() {
        let arr = [];
        let lastWord = guessedWords.slice(-1)[0] || "";

        for (let i = 0; i < lastWord.length; i++) {
            if (solution[i] === lastWord[i]) {
                arr.push({char: lastWord[i], index: i})
            }
        }
        return arr
    }

    function getWrongLetters() {
        return [...guessedWords.join("")].filter((i) => !solution.includes(i))
    }

    // function getPositionLetters() {
    //     return [];
    // }

    const checkHardMode = () => {
        let wrongChars = getWrongLetters();
        let correctChars = getCorrectLetters();
        // let positionChars = getPositionLetters();

        for (let char of inputWord) {
            if (wrongChars.join("").includes(char)) {
                error("ausgeschlossene Buchstaben");
                return false;
            }
        }

        for (let obj of correctChars) {
            if (inputWord[obj.index] !== obj.char) {
                error("bereits erratene Buchstaben nicht enthalten");
                return false
            }
        }

        // TODO check for guessed letters with wrong position

        return true;
    }

    const newGuess = () => {
        if (inputWord.length !== WORD_LENGTH) return error("Nicht genug Buchstaben")
        if (guessedWords.includes(inputWord)) return error("Bereits versucht")
        if (!wordlist.data.includes(inputWord)) return error("Unbekanntes Wort")
        if (hardMode && !checkHardMode()) return;

        setGuessedWords([...guessedWords, inputWord])
        setAlertMessage("")
        setInputWord("")

        if (inputWord === solution) {
            setGameWon(true);
            setGameRunning(false);
            setStats([...stats, {solution: solution, won: true, numGuesses: guessedWords.length + 1}])
        } else if (guessedWords.length + 2 > MAX_GUESSES) {
            setGameWon(false);
            setGameRunning(false);
            setStats([...stats, {solution: solution, won: false, numGuesses: guessedWords.length + 1}])
        }
    }

    useEffect(() => {
        localStorage.setItem("wordquiz-words", JSON.stringify(guessedWords))
    }, [guessedWords])
    useEffect(() => {
        localStorage.setItem("wordquiz-solution", JSON.stringify(solution))
        localStorage.setItem("wordquiz-hardmode", JSON.stringify(hardMode))
        localStorage.setItem("wordquiz-stats", JSON.stringify(stats))
    }, [hardMode, solution, stats])


    const handleInput = (event, key) => {
        //event.preventDefault()
        key = key.replace(/[^a-zäöü]+/i, "")
        if (!gameRunning) return
        switch (key) {
            case 'Enter':
                newGuess()
                break;
            case 'Backspace':
                setInputWord(prevState => prevState.slice(0, -1))
                break;
            default:
                if (inputWord.length >= WORD_LENGTH) break;
                if (key.length !== 1) break;
                setInputWord(prevState => prevState + key.valueOf().toLowerCase())
        }
    }

    const delay = ms => new Promise(res => setTimeout(res, ms));
    const error = async (message) => {
        setAlertMessage(message)
        setInputClass(prev => prev + ' shake')
        await delay(1000)
            .then(() => {
                setInputClass(prev => prev.replace(' shake', ''))
                setAlertMessage("")
            })
    }

    const reset = () => {
        setGuessedWords([])
        setInputWord('')
        setSolution(wordlist.data[Math.floor(Math.random() * wordlist.data.length)])
        setGameWon(false)
        setGameRunning(true)
    }
    const setFocus = () => {
        inputField.current.focus()
    }

    return (
        <>
            <main className="center"
                  onKeyDown={(event) => (handleInput(event, event.key))}
                  onClick={setFocus}
            >

                <div className='game'>
                    <input
                        ref={inputField}
                        className="input-field-hidden"
                        type="text"
                        autoFocus
                    />
                    {guessRows}
                    {guessesLeft ? <InputWord word={inputWord} cName={inputClass}/> : ''}
                    {emptyRows}
                </div>
            </main>
            <footer>
                <button onClick={handleGuide}>{"Anleitung"}</button>
                <div className='mid'>
                    {!gameRunning && !gameWon && <h2 className={'reveal-solution'}>{solution.toUpperCase()}</h2>}
                    {!gameRunning && <button onClick={reset}>Nochmal</button>}
                    {(alertMessage !== "") && <Alert>{alertMessage}</Alert>}
                </div>
                <button onClick={handleStats}>{"Statistik"}</button>
            </footer>
            <Modal
                show={guideVisible}
                handleClose={handleGuide}
                title={"Anleitung"}
            >
                <p>
                    Errate in 6 Versuchen das richtige Wort mit 5 Buchstaben.
                    Graue Buchstaben sind nicht enthalten, orangene an anderer Stelle und grüne sind korrekt platziert.
                    Worte mit Umlauten sind enthalten.
                    Im strikten Modus müssen alle erratenen Buchstaben bei neuen Versuchen verwendet werden.
                </p>
                <Switch title='Stikter Modus' toggle={handleHardMode} state={hardMode}/>
            </Modal>
            <Modal
                show={statsVisible}
                handleClose={handleStats}
                title="Statistik"
            >
                {stats.length ?
                    <>
                        <p>{`Anzahl Spiele: ${stats.length}`}</p>
                        <p>{`gewonnene Spiele: ${stats.filter((stat) => (stat.won === true)).length}`}</p>
                        <p>{`verlorene Spiele: ${stats.filter((stat) => (stat.won === false)).length}`}</p>
                        <p>{`durchschnittliche Versuche: ${Math.round(stats.reduce((prev, next) => prev + next.numGuesses, 0) / stats.length * 100) / 100 || 0}`}</p>
                    </>
                    :
                    <p>Noch keine Spiele beendet</p>
                }
            </Modal>
        </>);
}
