export function getFormattedTime(minutes) {
    const h = Math.floor(minutes / 60)
    const m = minutes % 60
    const hours = h > 0 ? h + "h " : "";
    const min = (m) > 0 ? m + "min" : "";
    return `${hours}${min}`;
}

function draw(ranges) {
    return <svg className={'list-ranges'}>
        {ranges.map((r, i) =>
            <rect
                width="10%"
                height={2 + r}
                x={`${i * (18)}%`}
                key={i}
            />
        )}
    </svg>
}


export default function ListSession(props) {
    let shortStringDate = `${props.date.getDate()}/${props.date.getMonth() + 1}`
    let urlIcon = process.env.PUBLIC_URL + `/data/icons/${props.method.toLowerCase()}.svg`
    let duration = getFormattedTime(props.duration)

    return (
        <div
            className='list-session'
            id={props.id}
            onClick={(event) => {
                props.onClick(event, props.id)
            }}
        >
            <p className={'list-date'}>{shortStringDate}</p>
            <img className={'list-image'} src={urlIcon} alt='Icon'/>
            <div className={'list-title'}>
                <p className={'list-name'}>{props.name}</p>
                <p className={'list-method'}>{props.method}
                    <span className={'list-duration'}>{duration}</span>
                </p>
            </div>
            {draw(props.ranges)}
        </div>
    )
}
